}

.header-toast {
  background: #5915e1;
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  text-align: center;
  position: relative;
  height: 40px;
  padding: 0 14px;

  .white-close {
    position: absolute;
    right: 20px;
    top: 0;
    bottom: 0;
    margin: auto;
    cursor: pointer;
  }

  .toast-text {
    display: inline-block;
    // padding-left: 33px;
    // background: url(../images/laba.svg) left no-repeat;
    height: 40px;
    overflow: hidden;

    ul {
      display: block;
      height: 40px;
      line-height: 22px;
      margin: 0;
      position: relative;

      &.animate {
        -webkit-animation: 7.5s rowup linear infinite normal;
        animation: 7.5s rowup linear infinite normal;
      }
    }

    li {
      text-align: center;
      // line-height: 22px;
      // padding: 9px 0;
      // height: 40px;
      // display: block;
      // text-align: left;
    }

    @keyframes rowup {
      0% {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
      }

      5% {
        -webkit-transform: translate3d(0, -40px, 0);
        transform: translate3d(0, -40px, 0);
      }

      33% {
        -webkit-transform: translate3d(0, -40px, 0);
        transform: translate3d(0, -40px, 0);
      }

      38% {
        -webkit-transform: translate3d(0, -80px, 0);
        transform: translate3d(0, -80px, 0);
      }

      66% {
        -webkit-transform: translate3d(0, -80px, 0);
        transform: translate3d(0, -80px, 0);
      }

      71% {
        -webkit-transform: translate3d(0, -120px, 0);
        transform: translate3d(0, -120px, 0);
      }

      100% {
        -webkit-transform: translate3d(0, -120px, 0);
        transform: translate3d(0, -120px, 0);
      }
    }

    a {
      color: #fff;
      line-height: 22px;
      padding: 9px 0;
      height: 40px;
      display: block;
      text-align: center;
    }

    a .link {
      font-weight: normal;
      font-size: 12px;
      line-height: 16px;
      color: #30084c;
      background: #ffffff;
      border-radius: 23px;
      padding: 2px 10px;
      margin-left: 15px;
      display: inline-block;
      height: 19px;
      vertical-align: top;
      margin-top: 2px;
    }
  }
}

#root .top-menu-bar .inner-box {
  font-weight: 500;
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 34px;
  // width: 700px;
  flex-shrink: 0;

  & > * {
    &:not(:last-child) {
      margin-right: 32px;
    }
  }

  button {
    height: 34px;
    font-size: 16px;
    background: transparent;
    margin: 0;
    padding: 0;
    min-width: 0px !important;

    &:hover {
      background: #5915e1;
      color: #fff !important;
    }
  }

  .menu-hover-dropdown {
    position: relative;
    // margin-right: 20px;
    cursor: pointer;
    height: 34px;
    font-size: 0;

    .down-icon {
      margin-left: 6px;
      height: 10px;
      width: 10px;
      background-image: url(../images/down-white.svg);
      background-repeat: no-repeat;
      transition: transform 0.2s;
    }

    &:hover .down-icon {
      transform: rotate(-180deg);

      // transform: rotate(-180deg);//, rotateY(-36px);
      // transform: rotateY(-36px);
    }

    .menu-icon {
      width: 68px;
      height: 100%;
      background: url(../images/menu-btn.svg) no-repeat;
      min-width: unset;
      border-radius: 5px;

      &:hover {
        background: url(../images/menu-btn-hover.svg) no-repeat;
      }
    }

    &:hover .header-select-box {
      display: block;
    }

    .header-select-box-wrapper {
      position: absolute;
      width: 180px;
    }

    .header-select-box,
    .header-select-box-pool {
      display: none;
      font-size: 14px;
      position: absolute;
      left: 0;
      border-radius: 5px;
      z-index: 3;

      .lang {
        width: 110px;
      }

      .header-wrapper {
        background: #fff;
        border-radius: 5px;
        box-shadow: 0px 9px 18px rgba(166, 166, 166, 0.2);
      }

      .h15 {
        height: 6px;
      }

      .account-basic-info {
        margin-top: 10px;
        padding-bottom: 10px;
      }

      .line {
        color: #000;
        margin-top: 0;
        margin-bottom: 15px;
      }

      li {
        display: block;
        margin: 0;
        text-align: left;
        // height: 28px;
        line-height: 40px;
        padding: 0 16px;
        border-radius: 5px;
      }

      li.selected,
      li:hover {
        background-color: rgba(89, 21, 225, 0.15) !important;
      }

      li:hover a {
        color: #5916e1;
      }

      li.top-selected,
      li.top-selected:hover {
        background: none;
        color: #5916e1;
        font-weight: bold;

        a {
          color: #5916e1;
        }
      }

      li a {
        margin: 0;
        padding: 0;
        width: 100%;
        display: inline-block;
        font-weight: normal;
      }

      .full-list,
      .top-list {
        padding: 2px;
        margin-bottom: 18px;
        position: relative;

        // comment out divider
        // &::after {
        //   content: '';
        //   display: block;
        //   padding: 0 10px;
        //   border-bottom: 1px solid #ccc;
        //   position: absolute;
        //   bottom: -5px;
        //   width: calc(100% - 20px);
        //   left: 10px;
        // }
      }
    }

    .sunswap-header-select {
      width: 110px;
      ul {
        li {
          text-align: center;
        }
      }
    }

    .header-select-box-lang {
      left: auto;
      right: 0;
    }

    .header-select-box-pool {
      &.show {
        display: block;
      }
    }
  }

  a {
    margin: 0;
    text-decoration: none;
    color: #fff;
    padding: 6px 18px;
    height: 100%;
    display: inline-flex;
    align-items: center;
    border-radius: 4px;
    font-size: 16px;
    height: 34px;

    &.is-hover-purple.active {
      background: #5915e1;
      color: #fff !important;
    }

    &.disabled {
      color: #675d72 !important;
      cursor: not-allowed;
    }

    &.menu-title {
      pointer-events: none;
      margin-left: 0;
      // padding: 5px;
      // background: red;

      &:hover {
        background: #5915e1;
        color: #fff !important;
      }
    }

    &.new,
    &.hot {
      margin-right: -18px;
    }

    &.is-hover-purple:hover {
      background: #5915e1;
      color: #fff !important;
    }
  }

  .top-list a {
    color: #333;

    &.active {
      color: #5915e1;
    }
  }

  .top-list .selected a,
  .full-list .selected a {
    color: #fff;
  }

  .full-list {
    a {
      color: #333;
      font-weight: normal;
    }
  }

  .address-text {
    color: #5915e1;
    font-weight: normal;
  }

  .language {
    span {
      cursor: pointer;
      color: #888090;
    }

    span.active {
      color: #fff;
    }
  }
}

