      }

      // .tvl-title {
      //   width: 146px;
      // }

      // .banner-tvl-mining {
      //   .tvl-title {
      //     margin: 0 auto;
      //     width: 124px;
      //   }
      // }
    }

    .banner-title {
      margin-top: 4%;
    }

    .banner-des {
      font-size: 14px;
      line-height: 25px;
      margin: 6px 20px 0;
    }

    .banner-audit {
      // width: calc(100% - 30px);
      // height: 48px;
      line-height: 24px;

      a {
        display: block;
      }
    }
  }

  .trans-notify {
    display: block;

    .typo-text-link {
      margin-right: 10px;
    }
  }

  #root footer {
    .pc-risk {
      display: none;
    }
  }

  .header-toast {
    .white-close {
      right: 10px;
    }

    .toast-text {
      display: inline-block;
      // padding-left: 33px;
      // background: url(../images/laba.svg) left no-repeat;
      height: 40px;
      overflow: hidden;
      margin-left: -25px;

      ul {
        display: block;
        height: 40px;
        line-height: 20px;
        margin: 0;
        position: relative;
        padding: 0;

        &.animate {
          -webkit-animation: 7.5s rowup linear infinite normal;
          animation: 7.5s rowup linear infinite normal;
        }
      }

      li {
        // line-height: 22px;
        // padding: 9px 0;
        // height: 40px;
        // display: block;
        // text-align: left;
      }

      @keyframes rowup {
        0% {
          -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
        }

        5% {
          -webkit-transform: translate3d(0, -40px, 0);
          transform: translate3d(0, -40px, 0);
        }

        33% {
          -webkit-transform: translate3d(0, -40px, 0);
          transform: translate3d(0, -40px, 0);
        }

        38% {
          -webkit-transform: translate3d(0, -80px, 0);
          transform: translate3d(0, -80px, 0);
        }

        66% {
          -webkit-transform: translate3d(0, -80px, 0);
          transform: translate3d(0, -80px, 0);
        }

        71% {
          -webkit-transform: translate3d(0, -120px, 0);
          transform: translate3d(0, -120px, 0);
        }

        100% {
          -webkit-transform: translate3d(0, -120px, 0);
          transform: translate3d(0, -120px, 0);
        }
      }

      a {
        color: #fff;
        line-height: 20px;
        padding: 0;
        height: 40px;
        display: flex;
        justify-content: center;
        align-items: center;

        span {
          display: block;
          font-size: 12px;
          max-width: 290px;
        }
      }

      a .link {
        font-size: 12px;
        line-height: 15px;
        color: #30084c;
        background: #ffffff;
        border-radius: 23px;
        padding: 2px 10px;
        margin-left: 0;
        display: inline-block;
        height: 17px;
        vertical-align: top;
        margin-top: 2px;
        display: none;
      }
    }
  }
}

